.post{
    margin: 10px;
    max-width: 300px;
}

.title{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    margin: 5px 0 0 10px;
}

.discription
{
    text-align: center;
    font-size: 17px;
    margin-bottom: 10px;
}

.image{
    height: 300px;
    width: 300px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid black;
}

.userIcon{
    width: 30px;
    height: 30px;
}

.userBox{
    display: flex;
    margin-bottom: 10px;
}

.postForm{
    display: flex;
    border: 1px solid black;
    max-width: 300px;
    height: 40px;
    background-color: black;
    position: relative;
}

.formButton{
    height: 38px;
    border: 1px solid black;
    margin: 1px 0 0 1px;
    transition: .5s;
}

.formButton:hover{
    background-color: black;
    color: #fff;
}

.formTextarea{
    margin: 1px 0 0 1px;
    max-height: 32px;
    min-width: 250px;
}

.login{
    border: none;
}

.formControl.error span{
    color: none;
    margin: 5px 0 5px 5px;
}

.inputForm{
    position: absolute;
    top: -22px;
    left: -1px;
    max-width: 305px;
    display: flex;
}

.imageInput{
    max-width: 115px;
    margin-right: 5px;
}

.inputBox{
    display: flex;
}

.like{
    width: 25px;
    height: 25px;
}

.likeSpan{
    margin: 0 5px 5px 5px;
    font-size: 20px;
}

.likeBox{
    margin-left: 15px;
}